<template>
  <div class="w_h100 rule_box">
    <img
      class="red_envelope w_70"
      src="@/assets/images/red_envelope/red.png"
      v-if="step===1"
      @click="step++"
    >
    <div class="red_envelope w_70" style="top:52%;" v-else-if="step===2">
      <img class="w_100" src="@/assets/images/red_envelope/red1.png">
      <img class="red_btn w_60" src="@/assets/images/red_envelope/red_btn.jpg" @click="step++">
    </div>

    <div class="popup-container1" v-else-if="step===3">
      <img class="w_100" src="@/assets/images/result_bg.png">
      <div class="form-container">
        <img class="title w_80 d_block m_center pt10" src="@/assets/images/info-title.png" alt>
        <div class="bottom">
          <form>
            <input class="input" type="text" placeholder="中奖人姓名" v-model="username">
            <input class="input" type="number" placeholder="手机号码" v-model="phone">
            <div class="vcode-box input">
              <input class="" type="text" placeholder="验证码" v-model="vcode" maxlength="4">
              <a class="vcode-btn">
                <img class="vcode-bg" src="@/assets/images/btn-container.png" alt>
                <span class="vcode-text" @click="getVcode">
                  {{isVcodeSend ? '验证码已发送' : '获取验证码'}}
                  {{isVcodeSend ? countdown : ''}}
                </span>
              </a>
            </div>
            <button class="btn-submit" type="submit" @click.prevent>
              <img class="submit-bg" src="@/assets/images/btn-container.png" alt>
              <span class="submit-text" @click="collectPrize">立即领取</span>
            </button>
          </form>
        </div>
      </div>
    </div>

    <div class="result_box w_90" v-else-if="step===4">
      <img class="w_100" src="@/assets/images/result_bg.png">
      <div class="result_content w_90 h_90">
        <img src="@/assets/images/result_tit.png" class="w_100">
        <div class="result_info w_90 h_87 m_center flex size15">
          <img class="w_70 pt10" src="@/assets/images/result_tit1.png">
          <img class="w70 mt10" src="@/assets/images/user_avatar.jpg">
          <div class="t_center bold mt5">GU GU</div>
          <div class="w_90 m_center ptb5 size14 bold text_box">
            <div>产品SN：MUIOOQY</div>
            <div>奖品名称：一等奖——399元现金红包</div>
            <div>中奖编号：PJ190092934885855</div>
            <div>中奖时间：2019-04-10 19:09:00</div>
          </div>
          <div class="w_90 m_center ptb5 size14 bold text_box">
            <div>中奖会员：[M00029818]会员姓名</div>
            <div>会员手机：13001356008</div>
          </div>
          <div class="w_90 m_center ptb5 size14 bold text_box">
            <div>申请兑奖：已申请</div>
            <div>申请时间：2019-04-10 19:09:00</div>
          </div>
          <div class="w_90 m_center ptb5 size14 bold text_box">
            <div>是否发放：已发放</div>
            <div>发放时间：2019-04-10 19:09:00</div>
            <div>领取地址：微信-服务通知</div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      step: 1,
      isVcodeSend: false,
      countdown: 60,
      username: "",
      phone: null,
      vcode: null
    };
  },
  mounted() {
    // console.log(this.$wx);
  },
  methods: {
    go_page(url) {
      this.$router.push(url);
    },
    getVcode() {
      if (this.isVcodeSend === true) return;
      this.isVcodeSend = true;
      let that = this;
      let timer = setInterval(() => {
        that.countdown--;
        if (that.countdown === 0) {
          that.isVcodeSend = false;
          clearInterval(timer);
          timer = null;
        }
      }, 1000);
    },
    collectPrize() {
      if (this.username.length === 0) {
        return alert("请填写姓名");
      }
      if (!this.phone) {
        return alert("请填写手机号");
      }
      if (!this.vcode) {
        return alert("请填写验证码");
      }
      this.step = 4;
    }
  }
};
</script>
<style scoped>
.rule_box {
  background-image: url("../assets/images/red_envelope/bg.jpg");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
}
.red_envelope {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}
.red_btn {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  bottom: 10%;
}
.result_box {
  position: absolute;
  left: 50%;
  top: 58%;
  transform: translate(-50%, -50%);
}
.result_content {
  position: absolute;
  left: 50%;
  top: 48%;
  transform: translate(-50%, -50%);
}
.result_info {
  border-radius: 10px;
  border: 4px solid #e76516;
  flex-direction: column;
}
.text_box {
  border-top: 4px solid #e76516;
}

.popup-container1 {
  width: 90%;
  position: absolute;
  left: 0;
  right: 0;
  margin: 0 auto;
  top: 20%;
  padding: 20px;
}
.form-container {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 90%;
  height: 90%;
}
.form-container title{
}
.form-container .bottom {
  text-align: center;
  margin-top: 20px;
}
.form-container input {
  border: 0;
  background: #fff;
  border-radius: 5px;
  display: inline-block;
  padding:10px 15px;
  font-size: 14px;
  box-shadow: inset 0px 1px 4px 0px #666;
}
.form-container input::placeholder {
  font-size: 14px;
}
.form-container .input {
  width: 80%;
  margin-top: 20px;
}
.form-container .vcode-box {
  text-align: center;
  position: relative;
  width: 80%;
  display: inline-block;
}
.form-container .vcode-box input {
  width: 100%;
  display: block;
  padding-right: 30%;
}
.form-container .vcode-box a {
  position: absolute;
  display: inline-block;
  min-width: 30%;
  padding:11px 5px;
  top: 1px;
  right: 0px;
}
.form-container .vcode-bg {
  display: block;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 0;
}
.form-container .vcode-text {
  display: block;
  width: 100%;
  height: 100%;
  position: relative;
  z-index: 1;
  font-weight: bold;
  font-size: 10px;
}
.form-container .btn-submit {
  margin-top: 60px;
  display: inline-block;
  width: 150px;
  height: 40px;
  border: 0;
  background: none;
  position: relative;
}
.btn-submit .submit-bg {
  display: block;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 0;
}
.form-container .submit-text {
  display: block;
  width: 100%;
  height: 100%;
  line-height: 40px;
  position: relative;
  z-index: 1;
  font-weight: bold;
  font-size: 16px;
}
</style>


